          console.log("fuck" + err);
        };
      };
    },

    start() {
      console.log("re-rendered");
      var self = this;

      self.listen();
    }
  },

  created: function() {
    this.idDisplay = this.$route.params.idDisplay;
    this.idCine = this.$route.params.idCine;
